/** @format */

.BackDrop {
  position: absolute;
  width: 100%;
  height: 100vh;
  background-color: #24272c;
  opacity: 0.6;
  top: 0;
}

.EditText {
  position: fixed;
  bottom: 70px;
  right: 25px;
  margin-bottom: 20px;
  width: fit-content;
  padding: 10px;
  border-radius: 8px;
  background-color: #ffffff;
}

.EditText h1 {
  font-size: 20px;
  font-family: Roboto;
  letter-spacing: 1px;
}

.TextPopup {
  transition: cubic-bezier(0.4, 0, 1, 1);
  width: 90%;
  position: absolute;
  left: 50%;
  top: 20%;
  transform: translate(-50%, 50%);
  border-radius: 10px;
  background-color: #ffffff;
  padding: 20px;
}

.TextPopup label {
  padding: 10px;
  font-weight: lighter;
  font-size: 18px;
  color: white;
  font-family: Roboto;
}

textarea {
  padding: 10px;
  font-size: 18px;
  height: 150px;
  width: 100%;
  border-radius: 4px;
  background-color: #e7ebf2;
  border: none;
}
/* .Btn {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
} */

.Btn button {
  margin-top: 10px;
  border: none;
  outline: none;
}

.ApplyBtn {
  width: fit-content;
  padding: 10px 20px 10px 20px;
  border-radius: 4px;
  background-color: #12b0ee;
  font-family: Roboto;
  font-size: 14px;
  font-weight: bold;
  color: #ffffff;
}

.CancelBtn {
  margin-left: 20px;
  opacity: 0.5;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 500;
  color: #3b414a;
}
