.FontPanel {
  position: fixed;
  bottom: 0;
  /* margin: auto; */
  height: 55px;
  /* width: 90%; */
  background-color: white;
  display: grid;
  overflow-x: scroll;
  grid-template-columns: repeat(7, 1fr);
}

.Font_Family {
  padding: 0px 10px 10px 10px;
}

.Font_Family .DropDown {
  width: 106px;
  border-radius: 0 0 4px 4px;
  padding: 20px 20px 12px 12px;
  background-color: white;
}

.Font_Family .DropDown h3 {
  opacity: 0.7;
  font-family: Roboto;
  font-size: 15px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #24272c;
}

.Font_Family .DropDown_Content {
  position: absolute;
  bottom: 55px;
  width: 218px;
  height: 148px;
  border-radius: 7px 7px 0 0;
  background-color: #e7ebf2;
  overflow-y: scroll;
}

.Font_Family .DropDown_Content .Font {
  display: grid;
  padding: 12px;
}

.Font_Family .DropDown_Content .Font:hover h1 {
  color: #12b0ee;
}

.Font_Family .DropDown_Content .Font h1 {
  font-family: Roboto;
  font-size: 15px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #24272c;
}

/* #262626 */
.Font_Size {
  position: relative;
  margin: auto;
}

.Font_Size .DropDown_Content {
  position: absolute;
  bottom: 35px;
  width: 100%;
  height: 200px;
  border-radius: 5px 5px 0 0;

  background-color: #e7ebf2;
  overflow-y: scroll;
}

.Font_Size .Size {
  text-align: center;
  padding-top: 12px;
}

.Font_Size input {
  width: 100%;
  border: none;
  padding: 10px;
  outline: none;
  margin-top: 0px;
}

/* Font Style */

.Font_Style {
  margin: auto;
}

.Font_Style .DropDown {
  width: 50px;
  height: 58px;
  padding: 20px 10px 10px 10px;
}

.Font_Style .DropDown_Content {
  position: absolute;
  left: 0;
  bottom: 54px;
  width: 100%;
  border-radius: 5px 5px 0 0;
  background-color: #e7ebf2;
  overflow-x: scroll;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  margin: auto;
}

.Font_Style .DropDown_Content .Saperator {
  width: 1px;
  height: 80% !important;
  background-color: #bababa;
  margin: auto;
  align-self: center;
}

.Font_Style h1 {
  padding: 15px;
  font-size: 16px;
  text-align: center;
  font-family: Roboto;
}

.Font_Style h3 {
  font-family: Roboto;
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #24272c;
  text-align: center;
}

.Font_Style h3 span {
  font-size: 12px;
  font-weight: normal;
  font-style: italic;
}

.Font_Align {
  margin: 10px auto;
  padding: 10px;
}

.Font_Align div {
  margin: 3px;
  height: 2px;
  border-radius: 1px;
  background-color: #12b0ee;
}

.Font_Align .line1 {
  width: 16px !important;
}

.Font_Align .line2 {
  width: 10px !important;
}

/* .Font_Align .line3 {
  width: 10px !important;
} */

/* Font Color */

.Font_Color {
  margin: 15px auto;
}

.Font_Color .Color {
  width: 24px;
  height: 24px;
  border-radius: 4px;
  border: solid 1px #24272c;
  background-color: #24272c;
}

.Font_Color .Color input {
  opacity: 0;
  position: relative;
}

.Line_Height {
  position: relative;
  margin: auto;
}

input {
  margin-top: 0 !important;
  margin-bottom: 0 !important;
  padding-left: 0 !important;
}
