/** @format */

.Button_Container {
  position: absolute;
  bottom: 70px;
  right: 85px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.Button_Container div {
  margin: 10px;
}

.Button_Container svg {
  width: 16px;
  height: 16px;
}

.Button_Container button {
  /* width: 44px;
  height: 44px; */
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.3);
  background-color: #ffffff;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #24272c;
}
