/** @format */

.Header {
  padding: 21px 10px 15px 10px;
  display: grid;
  grid-template-columns: 10% 30% 30% 30%;
  background-color: #282c32;
}

a {
  color: white;
  text-decoration: none;
  cursor: pointer;
}

.Header div {
  margin: auto;
}

.Header_Text {
  margin-left: 20px !important;
  text-align: start;
}

.Header_Text .Title h1 {
  font-family: Roboto;
  font-size: 20px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #ffffff;
}

.Header_Text .Sub p {
  opacity: 0.7;
  font-family: Roboto;
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #ffffff;
}

.Next a {
  font-family: Roboto;
  padding: 9px 17px 9px 17px;
  border-radius: 4px;
  background-color: #12b0ee;
}

.Next a svg {
  margin-left: 10px;
  transform: rotateY(180deg);
}

.LeftArrow {
  align-self: center;
  margin: auto;
}

.IndexBtn {
  display: flex;
  justify-content: space-around;
}

.IndexBtn div {
}

.IndexBtn img {
  margin: auto;
  display: block;
  width: 50%;
}

.IndexDown img {
  transform: rotateX(180deg);
}
