/** @format */

.canvas_container {
  background-color: #262626;
  height: 100vh;
}

.Toggle_Preview {
  border-radius: 4px;
  background-color: #373b43;
  padding: 12px;
  width: fit-content;
  margin: 20px auto;
}

.Toggle_Preview p {
  color: white;
  font-family: Roboto, "sans-serif";

  font-size: 14px;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
}
