/** @format */

.Container {
  position: fixed;
  bottom: 0px;
  /* margin: auto; */
  height: 55px;
  /* width: 90%; */
  background-color: white;
  width: 100%;
  display: grid;
  grid-template-columns: 55% 40%;
}

.GeneralPanel {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.Color input {
  opacity: 0;
  position: relative;
}

.Fill,
.Stroke {
  margin: 15px auto;
}

.Color {
  width: 24px;
  height: 24px;
  border-radius: 4px;
  border: solid 1px #24272c;
  background-color: #24272c;
  z-index: 12;
}

.Spacing {
  width: 40px;
  margin: auto;
}

.Spacing .Parent {
  padding: 5px;
}

.Spacing .Line {
  width: 2px;
  height: 18px;
  border-radius: 1px;
  background-color: #24272c;
  align-self: center;
}

.Spacing .Rect {
  width: 12px;
  height: 6px;
  border-radius: 1px;
  background-color: #24272c;
  align-self: center;
}

.StrokeWidth {
  position: relative;
  width: 80%;
  height: 100%;
  padding: 10px;
  margin: auto;
}

/* .StrokeWidth .Parent {
  margin: 10px 10px;
} */
.StrokeWidth .Parent div {
  background-color: black;
  width: 100%;
  margin-bottom: 6px;
  margin: 6px auto;
}

.StrokeWidth .Parent div:nth-child(1) {
  height: 1px;
}

.StrokeWidth .Parent div:nth-child(2) {
  height: 2px;
}

.StrokeWidth .Parent div:nth-child(3) {
  height: 3px;
}

.StrokeWidth .DropDown_Content {
  position: absolute;
  left: 0;
  bottom: 48px;
  width: 100%;
  height: 200px;
  border-radius: 5px 5px 0 0;
  background-color: #e7ebf2;
  overflow-y: scroll;
  margin: auto;
}

.StrokeWidth .DropDown_Content p {
  text-align: center;
  padding-top: 12px;
  font-family: Roboto;
}
